import React, { useState, useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../App';
import '../components/styles/ReviewBoard.css';
import apiClient from '../utils/apiClient';

function ReviewBoard() {
    const { isLoggedIn, userInfo } = useContext(UserContext);
    const [reviews, setReviews] = useState([]);
    const [volunteerActivities, setVolunteerActivities] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [regionFilter, setRegionFilter] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isEditMode, setIsEditMode] = useState(false);
    const [currentReview, setCurrentReview] = useState(null);
    const [selectedImage, setSelectedImage] = useState(null); //   
    const itemsPerPage = 10;
    const navigate = useNavigate();

    useEffect(() => {
        fetchReviews();
    }, [currentPage]);

    const fetchReviews = async () => {
        try {
            const response = await apiClient.get('/api/reviews', {
                params: {
                    region: regionFilter || null, // regionFilter   null 
                    keyword: searchTerm || null, // searchTerm   null 
                    page: currentPage - 1,
                    size: itemsPerPage,
                },
            });
            setReviews(response.data.content);
            console.log('Fetched reviews:', response.data);
        } catch (error) {
            console.error('    :', error);
        }
    };


    const handleViewReview = async (id) => {
        try {
            const response = await apiClient.get(`/api/reviews/${id}`);
            console.log('Review details:', response.data);
            navigate(`/reviews/${id}`, { state: { review: response.data } });
        } catch (error) {
            console.error('     :', error);
        }
    };


    const fetchVolunteerActivities = async () => {
        try {
            const response = await apiClient.get('/api/reviews/accepted-activities');
            setVolunteerActivities(response.data); //   
        } catch (error) {
            console.error('    :', error);
        }
    };

    const handleSearchChange = (e) => {
        setSearchTerm(e.target.value);
    };

    const handleRegionChange = (e) => {
        setRegionFilter(e.target.value);
    };

    const handlePageChange = (page) => {
        setCurrentPage(page);
    };

    const handleOpenModal = (review = null) => {
        if (!isLoggedIn) {
            alert(' .');
            return;
        }

        if (!isEditMode) {
            fetchVolunteerActivities();
        }

        if (review) {
            setIsEditMode(true);
            setCurrentReview(review);
        } else {
            setIsEditMode(false);
            setCurrentReview({
                title: '',
                content: '',
                region: '',
                volunteerId: '',
                imageUrl: '', //  URL  
            });
            setSelectedImage(null); //  
        }
        setIsModalOpen(true);
    };

    const handleCloseModal = () => {
        setIsModalOpen(false);
        setCurrentReview(null);
        setSelectedImage(null); //  
    };
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setCurrentReview((prev) => ({ ...prev, [name]: value }));
    };

    const handleImageChange = (e) => {
        const file = e.target.files[0];
        setSelectedImage(file);
    };

    const handleSaveReview = async () => {
        if (!currentReview.title.trim() || !currentReview.content.trim()) {
            alert('   .');
            return;
        }

        if (!currentReview.volunteerId) {
            alert(' .');
            return;
        }

        const formData = new FormData();
        formData.append('title', currentReview.title);
        formData.append('content', currentReview.content);
        formData.append('region', currentReview.region);
        formData.append('volunteerId', currentReview.volunteerId);
        if (selectedImage) {
            formData.append('image', selectedImage); //   
        }

        try {
            if (isEditMode) {
                await apiClient.put(`/api/reviews/${currentReview.id}`, formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
            } else {
                await apiClient.post(`/api/reviews/create`, formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
            }
            fetchReviews();
            handleCloseModal();
        } catch (error) {
            console.error('    :', error);
            if (error.response?.status === 403) {
                alert('   .');
            } else {
                alert('    .');
            }
        }
    };

    const filteredReviews = reviews.filter((review) =>
        review.title.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const totalPages = Math.ceil(filteredReviews.length / itemsPerPage);

    return (
        <div className="review-board-page">
            <h1 className="centered">  </h1>
            <div className="top-bar">
                <input
                    type="text"
                    placeholder=" ."
                    value={searchTerm}
                    onChange={handleSearchChange}
                    className="search-input"
                />
                <select
                    value={regionFilter}
                    onChange={handleRegionChange}
                    className="region-select"
                >
                    <option value=""> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <button className="search-button" onClick={fetchReviews}></button>
                <button onClick={() => handleOpenModal()} className="create-button">
                     
                </button>
            </div>
            <table className="board-table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {filteredReviews.map((review, index) => (
                        <tr key={review.id}>
                            <td>{index + 1}</td>
                            <td onClick={() => handleViewReview(review.id)}>{review.title}</td>
                            <td>{review.region || ''}</td>
                            <td>{review.username}</td>
                            <td>{new Date(review.createdAt).toLocaleString()}</td>
                            <td>{review.views || 0}</td>
                            <td>{review.commentsCount || 0}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
            <div className="pagination">
                {Array.from({ length: totalPages }, (_, index) => (
                    <button
                        key={index + 1}
                        onClick={() => handlePageChange(index + 1)}
                        className={currentPage === index + 1 ? 'active' : ''}
                    >
                        {index + 1}
                    </button>
                ))}
            </div>
            {isModalOpen && (
                <div className="modal">
                    <div className="modal-content">
                        <h2>{isEditMode ? ' ' : ' '}</h2>
                        <input
                            type="text"
                            name="title"
                            placeholder=""
                            value={currentReview.title}
                            onChange={handleInputChange}
                        />
                        <select
                            name="region"
                            value={currentReview.region}
                            onChange={handleInputChange}
                            className="region-select"
                        >
                            <option value=""> </option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                        {!isEditMode && (
                            <select
                                name="volunteerId"
                                value={currentReview.volunteerId}
                                onChange={handleInputChange}
                                className="volunteer-select"
                            >
                                <option value="" disabled> </option>
                                {volunteerActivities.map((activity) => (
                                    <option key={activity.id} value={activity.id}>{activity.title}</option>
                                ))}
                            </select>
                        )}
                        <textarea
                            name="content"
                            placeholder=" ."
                            value={currentReview.content}
                            onChange={handleInputChange}
                            rows="5"
                        ></textarea>
                        <input
                            type="file"
                            accept="image/*"
                            onChange={handleImageChange}
                        />
                        <button onClick={handleSaveReview}></button>
                        <button onClick={handleCloseModal}></button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default ReviewBoard;