import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import apiClient from '../utils/apiClient';
import '../components/styles/SellProductPage.css';

function SellProductPage() {
    const [activeTab, setActiveTab] = useState('sell'); //    
    const navigate = useNavigate();
    const [products, setProducts] = useState([]); //   
    const [editingProduct, setEditingProduct] = useState(null); //    
    const [likedProducts, setLikedProducts] = useState([]); //   
    const [selectedLikedProduct, setSelectedLikedProduct] = useState(null); //     
    const [transactions, setTransactions] = useState({ purchases: [], sales: [], pending: [] }); // /  

    //   
    const [productData, setProductData] = useState({
        title: '',
        description: '',
        price: '',
        category: '',
        image: null,
    });

    //   
    const handleTabChange = (tab) => {
        setActiveTab(tab);
        setSelectedLikedProduct(null); //       
        setEditingProduct(null); //    
    };

    //   
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setProductData((prev) => ({ ...prev, [name]: value }));
    };

    const handleImageChange = (e) => {
        setProductData((prev) => ({ ...prev, image: e.target.files[0] }));
    };

    //     
    const handleLikedProductClick = (product) => {
        setSelectedLikedProduct(product);
    };


    //   
    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const formData = new FormData();
            formData.append('title', productData.title);
            formData.append('description', productData.description);
            formData.append('price', productData.price);
            formData.append('category', productData.category);
            if (productData.image) {
                formData.append('image', productData.image);
            }

            await apiClient.post('/api/products', formData, {
                headers: { 'Content-Type': 'multipart/form-data' },
            });

            alert('  .');
            setProductData({ title: '', description: '', price: '', category: '', image: null });
            fetchProducts();
        } catch (error) {
            console.error('    :', error);
            alert('    .');
        }
    };

    //   
    const fetchProducts = async () => {
        try {
            const response = await apiClient.get('/api/products/user');
            setProducts(response.data);
        } catch (error) {
            console.error('    :', error);
        }
    };

    // 
    const handleCancelPurchase = async (transactionId) => {
        try {
            const response = await apiClient.post(`/api/transactions/${transactionId}/cancel`);
            if (response.status === 200) {
                alert('  .');
                fetchTransactions(); //    
            } else {
                alert('  .');
            }
        } catch (error) {
            console.error('    :', error);
            alert('    .');
        }
    };


    //   
    const fetchLikedProducts = async () => {
        try {
            const response = await apiClient.get('/api/products/liked');
            setLikedProducts(response.data);
        } catch (error) {
            console.error('     :', error);
        }
    };

    // //  
    const fetchTransactions = async () => {
        try {
            //     
            const transactionResponse = await apiClient.get('/api/transactions');
            const currentUserId = 1; //   ID  .
    
            let purchases = [];
            let pending = [];
            if (Array.isArray(transactionResponse.data)) {
                //   
                purchases = transactionResponse.data.filter((t) => t.buyer && t.status === ' ');
    
                //   
                pending = transactionResponse.data.filter((t) => t.buyer && t.status === ' ');
            } else {
                console.error('Unexpected response format for transactions:', transactionResponse.data);
            }
    
            //  
            const salesResponse = await apiClient.get('/api/products/sales');
            let sales = [];
            if (Array.isArray(salesResponse.data)) {
                sales = salesResponse.data; //    
            } else {
                console.error('Unexpected response format for sales:', salesResponse.data);
            }
    
            //  
            setTransactions({ purchases, pending, sales });
        } catch (error) {
            console.error('/     :', error);
        }
    };
    
    
    

    useEffect(() => {
        fetchTransactions();
    }, []);

    useEffect(() => {
        fetchProducts();
        fetchLikedProducts();
        fetchTransactions();
    }, []);

    //    
    const handleStatusChange = async (productId, newStatus) => {
        try {
            await apiClient.put(`/api/products/${productId}`, { status: newStatus });
            setProducts((prev) => prev.map((p) => (p.id === productId ? { ...p, status: newStatus } : p)));
        } catch (error) {
            console.error('   :', error);
        }
    };

    //   
    const handleDelete = async (productId) => {
        if (!window.confirm(' ?')) return;

        try {
            await apiClient.delete(`/api/products/${productId}`);
            setProducts((prev) => prev.filter((p) => p.id !== productId));
        } catch (error) {
            console.error('    :', error);
        }
    };

    //    
    const handleEditClick = (product) => {
        setEditingProduct(product);
        setProductData({
            title: product.title,
            description: product.description,
            price: product.price,
            category: product.category,
            image: null,
        });
    };

    //   /  
    const handleApprovePurchase = async (transactionId, approve) => {
        try {
            const response = await apiClient.post(`/api/transactions/${transactionId}/approve`, { approve });
            if (response.status === 200) {
                alert(approve ? '  .' : '  .');
                fetchTransactions(); //    
            } else {
                alert('   .');
            }
        } catch (error) {
            console.error('     :', error);
            alert('     .');
        }
    };

    //    
    const handleUpdate = async (e) => {
        e.preventDefault();

        try {
            const formData = new FormData();
            formData.append('title', productData.title);
            formData.append('description', productData.description);
            formData.append('price', productData.price);
            formData.append('category', productData.category);
            if (productData.image) {
                formData.append('image', productData.image);
            }

            await apiClient.put(`/api/products/${editingProduct.id}`, formData, {
                headers: { 'Content-Type': 'multipart/form-data' },
            });

            alert('  .');
            setEditingProduct(null);
            fetchProducts();
        } catch (error) {
            console.error('    :', error);
            alert('    .');
        }
    };

    return (
        <div className="product-management-page">
            <div className="tabs">
                <button onClick={() => handleTabChange('sell')} className={activeTab === 'sell' ? 'active' : ''}> </button>
                <button onClick={() => handleTabChange('manage')} className={activeTab === 'manage' ? 'active' : ''}> </button>
                <button onClick={() => handleTabChange('liked')} className={activeTab === 'liked' ? 'active' : ''}> </button>
                <button onClick={() => setActiveTab('transactions')} className={activeTab === 'transactions' ? 'active' : ''}>/ </button>
            </div>

            {activeTab === 'sell' && (
                <div className="sell-product-page">
                    <h1 className="page-title"> </h1>
                    <form className="sell-form" onSubmit={handleSubmit}>
                        <div className="form-group">
                            <label htmlFor="title"></label>
                            <input
                                type="text"
                                id="title"
                                name="title"
                                placeholder=" "
                                value={productData.title}
                                onChange={handleInputChange}
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="description"> </label>
                            <textarea
                                id="description"
                                name="description"
                                placeholder="  "
                                value={productData.description}
                                onChange={handleInputChange}
                                rows="4"
                                required
                            ></textarea>
                        </div>
                        <div className="form-group">
                            <label htmlFor="price"></label>
                            <input
                                type="number"
                                id="price"
                                name="price"
                                placeholder="  ()"
                                value={productData.price}
                                onChange={handleInputChange}
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="category"></label>
                            <select
                                id="category"
                                name="category"
                                value={productData.category}
                                onChange={handleInputChange}
                                required
                            >
                                <option value=""> </option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value="  ">  </option>
                                <option value=" "> </option>
                                <option value=""></option>
                            </select>
                        </div>
                        <div className="form-group">
                            <label htmlFor="image"> </label>
                            <input
                                type="file"
                                id="image"
                                accept="image/*"
                                onChange={handleImageChange}
                            />
                        </div>
                        <button type="submit" className="submit-button">
                            
                        </button>
                    </form>
                </div>
            )}

            {activeTab === 'manage' && (
                <div className="manage-products">
                    <h1 className="page-title"> </h1>
                    {products.length === 0 ? (
                        <p>  .</p>
                    ) : (
                        <ul className="product-list">
                            {products.map((product) => (
                                <li key={product.id} className="product-item">
                                    <img src={product.imageUrl} alt={product.title} className="product-image" />
                                    <h3>{product.title}</h3>
                                    <p>: {product.price}</p>
                                    <p>: {product.status}</p>

                                    <button onClick={() => handleEditClick(product)} className="edit-button"></button>
                                    <button onClick={() => handleDelete(product.id)} className="delete-button"></button>
                                </li>
                            ))}
                        </ul>
                    )}
                </div>
            )}

            {editingProduct && (
                <div className="edit-product-page">
                    <h1 className="page-title"> </h1>
                    <form onSubmit={handleUpdate} className="edit-form">
                        <div className="form-group">
                            <label htmlFor="title"></label>
                            <input
                                type="text"
                                id="title"
                                name="title"
                                placeholder=" "
                                value={productData.title}
                                onChange={handleInputChange}
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="description"> </label>
                            <textarea
                                id="description"
                                name="description"
                                placeholder="  "
                                value={productData.description}
                                onChange={handleInputChange}
                                rows="4"
                                required
                            ></textarea>
                        </div>
                        <div className="form-group">
                            <label htmlFor="price"></label>
                            <input
                                type="number"
                                id="price"
                                name="price"
                                placeholder="  ()"
                                value={productData.price}
                                onChange={handleInputChange}
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="category"></label>
                            <select
                                id="category"
                                name="category"
                                value={productData.category}
                                onChange={handleInputChange}
                                required
                            >
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value="  ">  </option>
                                <option value=" "> </option>
                                <option value=""></option>
                            </select>
                        </div>
                        <div className="form-group">
                            <label htmlFor="image"> </label>
                            <input
                                type="file"
                                id="image"
                                accept="image/*"
                                onChange={handleImageChange}
                            />
                        </div>
                        <button type="submit" className="submit-button">
                            
                        </button>
                        <button type="button" onClick={() => setEditingProduct(null)} className="cancel-button">
                            
                        </button>
                    </form>
                </div>
            )}

            {activeTab === 'liked' && (
                <div className="liked-products">
                    <h1 className="page-title"> </h1>
                    {selectedLikedProduct ? (
                        <div className="liked-product-detail">
                            <img src={selectedLikedProduct.imageUrl} alt={selectedLikedProduct.title} className="liked-product-image" />
                            <h3>{selectedLikedProduct.title}</h3>
                            <p>: {selectedLikedProduct.price}</p>
                            <p>: {selectedLikedProduct.status}</p>
                            <p>: {selectedLikedProduct.description}</p>
                            <button onClick={() => setSelectedLikedProduct(null)} className="back-button"></button>
                        </div>
                    ) : (
                        <ul className="liked-product-list">
                            {likedProducts.length === 0 ? (
                                <p>  .</p>
                            ) : (
                                likedProducts.map((product) => (
                                    <li key={product.id} className="liked-product-item" onClick={() => handleLikedProductClick(product)}>
                                        <img src={product.imageUrl} alt={product.title} className="liked-product-image" />
                                        <h3>{product.title}</h3>
                                        <p>: {product.price}</p>
                                    </li>
                                ))
                            )}
                        </ul>
                    )}
                </div>
            )}

            {activeTab === 'transactions' && (
                <div className="transaction-history">
                    <h1 className="page-title">/ </h1>
                    <div className="transactions-section">
                        <h2> </h2>
                        {transactions.purchases.length === 0 ? (
                            <p>  .</p>
                        ) : (
                            <ul className="transaction-list">
                                {transactions.purchases.map((transaction) => (
                                    <li key={transaction.id} className="transaction-item">
                                        <p>: {transaction.productTitle}</p>
                                        <p>: {transaction.price}</p>
                                        <p> : {transaction.status}</p>
                                    </li>
                                ))}
                            </ul>
                        )}
                    </div>
                    <div className="transactions-section">
                        <h2> </h2>
                        {transactions.sales.length === 0 ? (
                            <p>  .</p>
                        ) : (
                            <ul className="transaction-list">
                                {transactions.sales.map((transaction) => (
                                    <li key={transaction.id} className="transaction-item">
                                        <p>: {transaction.productTitle}</p>
                                        <p>: {transaction.price}</p>
                                        <p>: {transaction.buyerName}</p>
                                        <p> : {transaction.status}</p>
                                    </li>
                                ))}
                            </ul>
                        )}
                    </div>
                    <div className="transactions-section">
                        <h2>  </h2>
                        {transactions.pending.length === 0 ? (
                            <p>   .</p>
                        ) : (
                            <ul className="transaction-list">
                                {transactions.pending.map((transaction) => (
                                    <li key={transaction.id} className="transaction-item">
                                        <p>: {transaction.productTitle}</p>
                                        <p>: {transaction.price}</p>
                                        <p>: {transaction.buyerName}</p>
                                        <p> : {transaction.status}</p>
                                        <button
                                            onClick={() => handleApprovePurchase(transaction.id, true)}
                                            className="approve-button"
                                        >
                                            
                                        </button>
                                        <button
                                            onClick={() => handleApprovePurchase(transaction.id, false)}
                                            className="reject-button"
                                        >
                                            
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        )}
                    </div>
                </div>
            )}

        </div>
    );
}

export default SellProductPage;
