import React, { useEffect, useState } from 'react';
import { Table, Button, Container } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import apiClient from './../utils/apiClient';

function NoticeBoard() {
    const [notices, setNotices] = useState([]);
    const navigate = useNavigate();

    useEffect(() => {
        //   
        const fetchNotices = async () => {
            try {
                const response = await apiClient.get('/api/public/notices?limit=3'); //   API 
                const noticesData = Array.isArray(response.data) ? response.data : [];
                setNotices(noticesData);
            } catch (error) {
                console.error('     :', error.message);
                setNotices([]);
            }
        };

        fetchNotices();
    }, []);

    const handleDetailClick = (id) => {
        navigate(`/notice-detail/${id}`); //    
    };

    return (
        <Container style={{ marginTop: '40px' }}>
            <h2 className="text-center mb-4" style={{ color: '#4CAF50' }}>
                
            </h2>
            {notices.length > 0 ? (
                <Table striped bordered hover responsive>
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {notices.map((notice) => (
                            <tr key={notice.id}>
                                <td>{notice.id}</td>
                                <td>{notice.title}</td>
                                <td>{new Date(notice.createdTime).toLocaleDateString()}</td>
                                <td>
                                    <Button
                                        variant="outline-success"
                                        size="sm"
                                        onClick={() => handleDetailClick(notice.id)}
                                    >
                                        
                                    </Button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            ) : (
                <p className="text-center"> .</p>
            )}
        </Container>
    );
}

export default NoticeBoard;
