import React, { useState, useEffect } from 'react';
import apiClient from '../utils/apiClient';
import '../components/styles/AdminPage.css';
import { Container, Row, Col, Button, Table, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

function AdminPage() {
    const [activeTab, setActiveTab] = useState('inquiries'); //   :  
    const [isAdmin, setIsAdmin] = useState(false); //   
    const [loading, setLoading] = useState(true); //   
    const navigate = useNavigate();

    //   
    const [inquiries, setInquiries] = useState([]);
    const [replyContent, setReplyContent] = useState('');
    const [editingReplyId, setEditingReplyId] = useState(null);

    //   
    const [notices, setNotices] = useState([]);
    const [newNotice, setNewNotice] = useState({ title: '', content: '' });
    const [editingNotice, setEditingNotice] = useState(null);

    //  
    useEffect(() => {
        const checkAdminStatus = async () => {
            try {
                const response = await apiClient.get('/api/users/session');
                const user = response.data;
                if (user.role === 'ADMIN') {
                    setIsAdmin(true);
                } else {
                    alert('  .');
                    navigate('/');
                }
            } catch (error) {
                console.error('  :', error);
                alert('  .');
                navigate('/');
            } finally {
                setLoading(false);
            }
        };

        checkAdminStatus();
    }, [navigate]);

    //   
    useEffect(() => {
        if (isAdmin && activeTab === 'inquiries') fetchInquiries();
        if (isAdmin && activeTab === 'notices') fetchNotices();
    }, [isAdmin, activeTab]);

    const fetchInquiries = async () => {
        try {
            const response = await apiClient.get('/api/admin/inquiries');
            setInquiries(response.data);
        } catch (error) {
            console.error('    :', error);
        }
    };

    const fetchNotices = async () => {
        try {
            const response = await apiClient.get('/api/admin/notices');
            setNotices(response.data);
        } catch (error) {
            console.error('    :', error);
        }
    };

    //   /
    const handleReply = async (inquiryId, isEdit = false) => {
        try {
            const endpoint = isEdit
                ? `/api/admin/inquiries/${inquiryId}/edit-reply`
                : `/api/admin/inquiries/${inquiryId}/reply`;

            const response = await apiClient.post(endpoint, { reply: replyContent }, {
                headers: { 'Content-Type': 'application/json' },
            });

            alert(isEdit ? ' .' : ' .');
            setReplyContent('');
            setEditingReplyId(null);
            fetchInquiries(); //  
        } catch (error) {
            console.error('  :', error.response || error);
            alert('  .');
        }
    };

    //  
    const handleAddNotice = async () => {
        try {
            const response = await apiClient.post('/api/admin/notices', newNotice);
            setNotices([...notices, response.data]);
            setNewNotice({ title: '', content: '' });
            alert(' .');
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    //  
    const handleDeleteNotice = async (noticeId) => {
        try {
            await apiClient.delete(`/api/admin/notices/${noticeId}`);
            setNotices(notices.filter((notice) => notice.id !== noticeId));
            alert(' .');
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    //  
    const handleEditNotice = async () => {
        try {
            await apiClient.put(`/api/admin/notices/${editingNotice.id}`, editingNotice);
            setNotices(
                notices.map((notice) =>
                    notice.id === editingNotice.id ? editingNotice : notice
                )
            );
            setEditingNotice(null);
            alert(' .');
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    if (loading) {
        return <p> ...</p>;
    }

    if (!isAdmin) {
        return null;
    }

    return (
        <Container className="admin-page">
            <Row className="mb-4">
                <Col>
                    <h1 className="admin-title"> </h1>
                </Col>
            </Row>
            <Row className="mb-3">
                <Col>
                    <div className="tab-buttons">
                        <Button
                            variant={activeTab === 'inquiries' ? 'primary' : 'outline-primary'}
                            onClick={() => setActiveTab('inquiries')}
                        >
                              
                        </Button>
                        <Button
                            variant={activeTab === 'notices' ? 'primary' : 'outline-primary'}
                            onClick={() => setActiveTab('notices')}
                            className="ms-2"
                        >
                             
                        </Button>
                    </div>
                </Col>
            </Row>
            <Row>
                <Col>
                    {activeTab === 'inquiries' && (
                        <div className="tab-content">
                            <h2 className="tab-title">  </h2>
                            <Table striped bordered hover responsive className="admin-table">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th> </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {inquiries.map((inquiry) => (
                                        <tr key={inquiry.id}>
                                            <td>{inquiry.id}</td>
                                            <td>{inquiry.name}</td>
                                            <td>{inquiry.content}</td>
                                            <td>{inquiry.category}</td>
                                            <td>{inquiry.reply ? ' ' : ''}</td>
                                            <td>
                                                {editingReplyId === inquiry.id ? (
                                                    <Form>
                                                        <Form.Control
                                                            as="textarea"
                                                            rows={3}
                                                            value={replyContent}
                                                            onChange={(e) => setReplyContent(e.target.value)}
                                                        />
                                                        <Button
                                                            className="mt-2"
                                                            variant="success"
                                                            onClick={() => handleReply(inquiry.id)}
                                                        >
                                                            
                                                        </Button>
                                                    </Form>
                                                ) : (
                                                    <>
                                                        {inquiry.reply ? (
                                                            <Button
                                                                className="ms-2"
                                                                variant="warning"
                                                                onClick={() => {
                                                                    setEditingReplyId(inquiry.id);
                                                                    setReplyContent(inquiry.reply); //      
                                                                }}
                                                            >
                                                                
                                                            </Button>
                                                        ) : (
                                                            <Button
                                                                variant="primary"
                                                                onClick={() => {
                                                                    setEditingReplyId(inquiry.id);
                                                                    setReplyContent(''); //     
                                                                }}
                                                            >
                                                                
                                                            </Button>
                                                        )}
                                                    </>
                                                )}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>


                            </Table>
                        </div>
                    )}
                    {activeTab === 'notices' && (
                        <div className="tab-content">
                            <h2 className="tab-title"> </h2>
                            <Form>
                                <Form.Group controlId="noticeTitle">
                                    <Form.Label></Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder=" "
                                        value={newNotice.title}
                                        onChange={(e) => setNewNotice({ ...newNotice, title: e.target.value })}
                                    />
                                </Form.Group>
                                <Form.Group controlId="noticeContent" className="mt-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control
                                        as="textarea"
                                        rows={5}
                                        placeholder=" "
                                        value={newNotice.content}
                                        onChange={(e) => setNewNotice({ ...newNotice, content: e.target.value })}
                                    />
                                </Form.Group>
                                <Button variant="success" className="mt-3" onClick={handleAddNotice}>
                                     
                                </Button>
                            </Form>
                            <Table striped bordered hover responsive className="mt-4">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {notices.map((notice) => (
                                        <tr key={notice.id}>
                                            <td>{notice.id}</td>
                                            <td>
                                                {editingNotice && editingNotice.id === notice.id ? (
                                                    <Form.Control
                                                        value={editingNotice.title}
                                                        onChange={(e) =>
                                                            setEditingNotice({
                                                                ...editingNotice,
                                                                title: e.target.value,
                                                            })
                                                        }
                                                    />
                                                ) : (
                                                    notice.title
                                                )}
                                            </td>
                                            <td>
                                                {editingNotice && editingNotice.id === notice.id ? (
                                                    <Form.Control
                                                        as="textarea"
                                                        value={editingNotice.content}
                                                        onChange={(e) =>
                                                            setEditingNotice({
                                                                ...editingNotice,
                                                                content: e.target.value,
                                                            })
                                                        }
                                                    />
                                                ) : (
                                                    notice.content
                                                )}
                                            </td>
                                            <td>
                                                {editingNotice && editingNotice.id === notice.id ? (
                                                    <Button
                                                        variant="success"
                                                        onClick={handleEditNotice}
                                                    >
                                                        
                                                    </Button>
                                                ) : (
                                                    <Button
                                                        variant="warning"
                                                        onClick={() => setEditingNotice(notice)}
                                                    >
                                                        
                                                    </Button>
                                                )}
                                            </td>
                                            <td>
                                                <Button
                                                    variant="danger"
                                                    onClick={() => handleDeleteNotice(notice.id)}
                                                >
                                                    
                                                </Button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </Table>
                        </div>
                    )}
                </Col>
            </Row>
        </Container>
    );
}

export default AdminPage;
