import React, { useEffect } from 'react';
import '../components/styles/AboutPage.css';

function AboutPage() {
  useEffect(() => {
    //   API   .
    const script = document.createElement('script');
    script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${process.env.REACT_APP_KAKAO_MAP_API_KEY}&autoload=false`;
    script.async = true;

    script.onload = () => {
      // Kakao  API .
      window.kakao.maps.load(() => {
        const container = document.getElementById('map'); //   div
        const options = {
          center: new window.kakao.maps.LatLng(37.5665, 126.9780), //     ()
          level: 3, //   
        };

        //   
        const map = new window.kakao.maps.Map(container, options);

        //  
        const markerPosition = new window.kakao.maps.LatLng(37.5665, 126.9780); //  
        const marker = new window.kakao.maps.Marker({
          position: markerPosition,
        });

        //    
        marker.setMap(map);

        //  
        const infoWindow = new window.kakao.maps.InfoWindow({
          content: '<div style="padding:5px;">PetPrice </div>', //  
        });

        //    
        window.kakao.maps.event.addListener(marker, 'click', () => {
          infoWindow.open(map, marker); //    
        });
      });
    };

    document.head.appendChild(script);
  }, []);

  return (
    <div>
      <div className="header-image">
        <div className="header-text">
          <h1>PET PRICE</h1>
          <h2>  , PetPrice .</h2>
        </div>
      </div>
      <div className="content">
        <h2></h2>
        <p>
          PetPrice    ,      .
                ,     
          .
        </p>
        <p>
                 ,     
                  . ,    ,
                  .
        </p>
        <p>
          PetPrice    ,        
          .     ,     
          .
        </p>
        <span className="highlight"> ,    .</span>
      </div>
      <div className="content">
        <h2></h2>
        <div id="map" style={{ width: '100%', height: '400px', marginTop: '20px' }}></div>
      </div>
    </div>
  );
}

export default AboutPage;
