import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import apiClient from './../utils/apiClient';
import '../components/styles/VolunteerDetail.css';

function VolunteerDetail() {
    const { id } = useParams();
    const navigate = useNavigate();
    const [volunteer, setVolunteer] = useState(null); //  
    const [participantDetails, setParticipantDetails] = useState([]); //   
    const [isEditing, setIsEditing] = useState(false); //   
    const [form, setForm] = useState({}); //   
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchVolunteerData = async () => {
            try {
                setLoading(true);
                setError(null);

                //   
                const response = await apiClient.get(`/api/volunteer/${id}`);
                setVolunteer(response.data);
                setForm(response.data); //    

                //    
                const participantsResponse = await apiClient.get(`/api/volunteer/${id}/participants/details`);
                setParticipantDetails(participantsResponse.data || []);
            } catch (err) {
                if (err.response?.status === 401 || err.response?.status === 403) {
                    alert(' .   .');
                    navigate('/');
                } else {
                    setError(err.response?.data || '    .');
                }
            } finally {
                setLoading(false);
            }
        };

        fetchVolunteerData();
    }, [id, navigate]);

    const handleParticipantAction = async (participantId, action) => {
        try {
            await apiClient.post(
                `/api/volunteer/${id}/participants/${participantId}/action`,
                null,
                { params: { action } }
            );

            //   
            setParticipantDetails((prev) =>
                prev.map((participant) =>
                    participant.id === participantId ? { ...participant, status: action } : participant
                )
            );
        } catch (err) {
            alert(err.response?.data || '    .');
        }
    };

    const handleEditToggle = () => {
        setIsEditing((prev) => !prev);
    };

    const handleFormChange = (e) => {
        const { name, value } = e.target;
        setForm((prev) => ({ ...prev, [name]: value }));
    };

    const handleFormSubmit = async () => {
        try {
            await apiClient.put(`/api/volunteer/${id}`, form);
            alert('.');
            setVolunteer(form); //   
            setIsEditing(false);
        } catch (err) {
            alert(err.response?.data || ' .');
        }
    };

    const handleDelete = async () => {
        try {
            await apiClient.delete(`/api/volunteer/${id}`);
            alert('  .');
            navigate('/mypage'); // 
        } catch (err) {
            alert(err.response?.data || '  .');
        }
    };

    if (loading) {
        return <p> ...</p>;
    }

    if (error) {
        return <p>: {error}</p>;
    }

    if (!volunteer) {
        return <p>    .</p>;
    }

    return (
        <div className="volunteer-detail-container">
            {isEditing ? (
                <div className="edit-form">
                    <h2> </h2>
                    <label>
                        :
                        <input
                            type="text"
                            name="title"
                            value={form.title || ''}
                            onChange={handleFormChange}
                        />
                    </label>
                    <label>
                        :
                        <textarea
                            name="content"
                            value={form.content || ''}
                            onChange={handleFormChange}
                        />
                    </label>
                    <label>
                        :
                        <input
                            type="text"
                            name="region"
                            value={form.region || ''}
                            onChange={handleFormChange}
                        />
                    </label>
                    <label>
                        :
                        <input
                            type="text"
                            name="contact"
                            value={form.contact || ''}
                            onChange={handleFormChange}
                        />
                    </label>
                    <label>
                         :
                        <input
                            type="date"
                            name="startDate"
                            value={form.startDate || ''}
                            onChange={handleFormChange}
                        />
                        ~
                        <input
                            type="date"
                            name="endDate"
                            value={form.endDate || ''}
                            onChange={handleFormChange}
                        />
                    </label>
                    <button onClick={handleFormSubmit} className="save-button"></button>
                    <button onClick={handleEditToggle} className="cancel-button"></button>
                </div>
            ) : (
                <>
                    <div className="volunteer-header">
                        <h1>{volunteer.title}</h1>
                        <div className="action-buttons">
                            <button onClick={handleEditToggle} className="edit-button"></button>
                            <button onClick={handleDelete} className="delete-button"></button>
                        </div>
                    </div>
                    <div className="volunteer-info">
                        <p><strong>:</strong> {volunteer.content}</p>
                        <p><strong>:</strong> {volunteer.region}</p>
                        <p><strong>:</strong> {volunteer.contact}</p>
                        <p><strong> :</strong> {volunteer.startDate} ~ {volunteer.endDate}</p>
                        <p><strong> :</strong> {volunteer.currentParticipants}</p>
                        <p><strong>  :</strong> {volunteer.maxParticipants}</p>
                    </div>
                    <h2>  </h2>
                    {participantDetails.length === 0 ? (
                        <p>   .</p>
                    ) : (
                        <table>
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {participantDetails.map((participant) => (
                                    <tr key={participant.id}>
                                        <td>{participant.username}</td>
                                        <td>{participant.phoneNumber}</td>
                                        <td>{participant.status || ' '}</td>
                                        <td>
                                            {participant.status !== '' && (
                                                <button
                                                    onClick={() => handleParticipantAction(participant.id, 'Accepted')}
                                                >
                                                    
                                                </button>
                                            )}
                                            {participant.status !== '' && (
                                                <button
                                                    onClick={() => handleParticipantAction(participant.id, 'Rejected')}
                                                >
                                                    
                                                </button>
                                            )}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    )}
                </>
            )}
        </div>
    );
}

export default VolunteerDetail;
