import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '../components/styles/AnimalSearchPage.css';

function AnimalSearchPage() {
    const [animals, setAnimals] = useState([]);
    const [filters, setFilters] = useState({
        bgnde: '',
        endde: '',
        upkind: '',
        upr_cd: '',
        org_cd: '',
        state: '',
        neuter_yn: '',
    });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [selectedAnimal, setSelectedAnimal] = useState(null); //   
    const [modalVisible, setModalVisible] = useState(false); //   

    const API_KEY = process.env.REACT_APP_ANIMAL_API_KEY; //  API 
    const KAKAO_MAP_API_KEY = process.env.REACT_APP_KAKAO_MAP_API_KEY; //  API 

    const handleSearch = async () => {
        setLoading(true);
        setError(null);

        try {
            const params = {
                serviceKey: API_KEY,
                bgnde: filters.bgnde?.replace(/-/g, '') || '',
                endde: filters.endde?.replace(/-/g, '') || '',
                upkind: filters.upkind || '',
                upr_cd: filters.upr_cd || '',
                org_cd: filters.org_cd || '',
                state: filters.state || '',
                neuter_yn: filters.neuter_yn || '',
                _type: 'json',
                pageNo: 1,
                numOfRows: 5, //  10  (,)   5 
            };

            const response = await axios.get(
                'http://apis.data.go.kr/1543061/abandonmentPublicSrvc/abandonmentPublic',
                { params }
            );

            const body = response?.data?.response?.body;
            if (body?.items?.item) {
                setAnimals(body.items.item);
            } else {
                setAnimals([]);
                setError(' .');
            }
        } catch (err) {
            console.error('    :', err);
            setError('    .');
        } finally {
            setLoading(false);
        }
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFilters((prevFilters) => ({
            ...prevFilters,
            [name]: value,
        }));
    };

    const handleAnimalClick = (animal) => {
        setSelectedAnimal(animal); //    
        setModalVisible(true); //  
    };

    const handleModalClose = () => {
        setModalVisible(false);
    };

    useEffect(() => {
        if (modalVisible && selectedAnimal) {
            //  API   
            const script = document.createElement('script');
            script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${KAKAO_MAP_API_KEY}&libraries=services&autoload=false`;
            script.async = true;

            script.onload = () => {
                window.kakao.maps.load(() => {
                    const container = document.getElementById('map');
                    const options = {
                        center: new window.kakao.maps.LatLng(37.5665, 126.9780),
                        level: 3,
                    };

                    const map = new window.kakao.maps.Map(container, options);

                    const geocoder = new window.kakao.maps.services.Geocoder();

                    //    
                    geocoder.addressSearch(selectedAnimal.careAddr, (result, status) => {
                        if (status === window.kakao.maps.services.Status.OK) {
                            const coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);

                            //   
                            map.setCenter(coords);

                            //  
                            new window.kakao.maps.Marker({
                                map,
                                position: coords,
                            });
                        }
                    });
                });
            };

            document.head.appendChild(script);
        }
    }, [modalVisible, selectedAnimal]);

    return (
        <div className="animal-search-page">
            <h1> </h1>

            {/*   */}
            <div className="search-bar">
                <input type="date" name="bgnde" onChange={handleInputChange} placeholder=" " />
                <input type="date" name="endde" onChange={handleInputChange} placeholder=" " />
                <select name="upkind" onChange={handleInputChange}>
                    <option value=""></option>
                    <option value="417000"></option>
                    <option value="422400"></option>
                    <option value="429900"></option>
                </select>
                {/*  ,       */}
                <select name="upr_cd" onChange={handleInputChange}>
                    <option value=""> </option>
                    <option value="6110000"></option>
                    <option value="6260000"></option>
                    <option value="6270000"></option>
                </select>
                <select name="state" onChange={handleInputChange}>
                    <option value=""></option>
                    <option value="notice"></option>
                    <option value="protect"></option>
                </select>
                <select name="neuter_yn" onChange={handleInputChange}>
                    <option value=""> </option>
                    <option value="Y"></option>
                    <option value="N"></option>
                    <option value="U"></option>
                </select>
                <button onClick={handleSearch}></button>
            </div>

            {/*   */}
            {loading && <p> ...</p>}

            {/*   */}
            {error && <p className="error">{error}</p>}

            {/*   */}
            <div className="animal-list">
                {animals.map((animal) => (
                    <div key={animal.desertionNo} className="animal-item" onClick={() => handleAnimalClick(animal)}>
                        <img src={animal.popfile} alt={animal.kindCd} className="animal-image" />
                        <div className="animal-info">
                            <h3>{animal.kindCd}</h3>
                            <p> : {animal.happenPlace}</p>
                            <p>: {animal.happenDt}</p>
                            <p>: {animal.careNm}</p>
                        </div>
                    </div>
                ))}
            </div>

            {/*  */}
            {modalVisible && selectedAnimal && (
    <div className="modal">
        <div className="modal-content">
            <span className="close" onClick={handleModalClose}>
                &times;
            </span>
            <div className="modal-body">
                {/*   */}
                <div className="modal-left">
                    <div className="animal-details-container">
                        <img
                            src={selectedAnimal.popfile}
                            alt={selectedAnimal.kindCd}
                            className="modal-animal-image"
                        />
                        <div className="animal-details">
                            <p><strong>:</strong> {selectedAnimal.desertionNo}</p>
                            <p><strong> :</strong> {selectedAnimal.happenDt}</p>
                            <p><strong> :</strong> {selectedAnimal.happenPlace}</p>
                        </div>
                    </div>
                </div>
                {/*   */}
                <div className="modal-right">
                    <div className="section-title"> </div>
                    <p><strong>:</strong> {selectedAnimal.kindCd}</p>
                    <p><strong>:</strong> {selectedAnimal.age}</p>
                    <p><strong>:</strong> {selectedAnimal.weight}</p>
                    <p><strong>:</strong> {selectedAnimal.sexCd}</p>
                    <p><strong> :</strong> {selectedAnimal.neuter_yn}</p>
                    <p><strong>:</strong> {selectedAnimal.colorCd}</p>
                    <p><strong>:</strong> {selectedAnimal.specialMark}</p>

                    <div className="section-title"> </div>
                    <p><strong> :</strong> {selectedAnimal.careNm}</p>
                    <p><strong> :</strong> {selectedAnimal.careAddr}</p>
                    <p><strong>:</strong> {selectedAnimal.careTel}</p>
                </div>
            </div>
            <div id="map" style={{ width: '100%', height: '300px' }}></div>
        </div>
    </div>
)}


        </div>
    );
}

export default AnimalSearchPage;
