import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import apiClient from '../utils/apiClient';
import '../components/styles/ProductPage.css';
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';

function ProductPage() {
    const [products, setProducts] = useState([]);
    const [categories, setCategories] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedCategory, setSelectedCategory] = useState('');
    const [selectedProduct, setSelectedProduct] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [stompClient, setStompClient] = useState(null); // Stomp Client 
    const [currentUser, setCurrentUser] = useState(null); //    
    const navigate = useNavigate();

    useEffect(() => {
        fetchProducts();
        fetchCategories();
        fetchCurrentUser(); //    
        initializeWebSocket();
    }, []);

    const initializeWebSocket = () => {
        const socket = new SockJS('/ws');
        const client = Stomp.over(socket);
        client.connect({}, () => {
            setStompClient(client);
        });
    };

    const fetchProducts = async () => {
        try {
            const response = await apiClient.get('/api/products');
            setProducts(response.data);
        } catch (error) {
            console.error('     :', error);
            alert('   .');
        }
    };

    const fetchCategories = async () => {
        try {
            setCategories(['', '', '', '', '  ', ' ', '']);
        } catch (error) {
            console.error('    :', error);
        }
    };

    const fetchCurrentUser = async () => {
        try {
            const response = await apiClient.get('/api/users/session'); //     API 
            setCurrentUser(response.data);
        } catch (error) {
            console.error('      :', error);
        }
    };

    const handleProductClick = (product) => {
        setSelectedProduct(product);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setSelectedProduct(null);
        setIsModalOpen(false);
    };

    const handleToggleLike = async (productId) => {
        try {
            await apiClient.post(`/api/products/${productId}/like`);
            fetchProducts();
        } catch (error) {
            console.error('     :', error);
            alert(' .');
        }
    };

    const handleBuyProduct = async () => {
        if (!selectedProduct) return;

        try {
            await apiClient.post(`/api/products/${selectedProduct.id}/purchase-request`);
            alert('   .');
            navigate('/payment', { state: { product: selectedProduct } }); //   
        } catch (error) {
            console.error('    :', error);
            alert('  .');
        }
    };

    const handleSellProduct = () => {
        navigate('/sell-product');
    };

    const handleChatStart = async () => {
        if (!stompClient || !selectedProduct || !currentUser) {
            alert('   .      .');
            return;
        }

        const roomKey = `${selectedProduct.id}-${selectedProduct.sellerId}`;
        const buyerId = currentUser.id;

        console.log('   :', {
            productId: selectedProduct.id,
            userId: buyerId,
        });

        try {
            const response = await apiClient.post('/api/chats/create', {
                productId: selectedProduct.id,
                userId: buyerId,
            });

            stompClient.send(
                '/app/chat.start',
                {},
                JSON.stringify({
                    room: roomKey,
                    productId: selectedProduct.id,
                    sellerId: selectedProduct.sellerId,
                    buyerId: buyerId,
                })
            );

            alert(` .  : ${response.data}`);
            navigate(`/chat/${roomKey}`);
        } catch (error) {
            console.error('   :', error);
            alert('  .');
        }
    };

    const filteredProducts = products.filter(
        (product) =>
            (selectedCategory === '' || product.category === selectedCategory || selectedCategory === '') &&
            product.title.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className="product-page">
            <div className="top-bar">
                <input
                    type="text"
                    placeholder=" "
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="search-input"
                />
                <select
                    value={selectedCategory}
                    onChange={(e) => setSelectedCategory(e.target.value)}
                    className="category-select"
                >
                    {categories.map((category) => (
                        <option key={category} value={category}>
                            {category}
                        </option>
                    ))}
                </select>
                <button onClick={handleSellProduct} className="sell-button">
                     
                </button>
            </div>

            <div className="product-grid">
                {filteredProducts.map((product) => (
                    <div
                        key={product.id}
                        className="product-card"
                        onClick={() => handleProductClick(product)}
                    >
                        <img
                            src={product.imageUrl || '/placeholder.png'}
                            alt={product.title}
                            className="product-image"
                        />
                        <h3 className="product-title">{product.title}</h3>
                        <p className="product-price">{product.price.toLocaleString()} </p>
                    </div>
                ))}
            </div>

            {isModalOpen && selectedProduct && (
                <div className="modal">
                    <div className="modal-content">
                        <img
                            src={selectedProduct.imageUrl || '/placeholder.png'}
                            alt={selectedProduct.title}
                            className="modal-image"
                        />
                        <h2 className="modal-title">{selectedProduct.title}</h2>
                        <p className="modal-date">: {new Date(selectedProduct.createdAt).toLocaleDateString()}</p>
                        <p className="modal-price">{selectedProduct.price.toLocaleString()} </p>
                        <p className="modal-description">{selectedProduct.description}</p>
                        <p className="modal-status">
                            <span className={`status-badge ${selectedProduct.status.toLowerCase().replace(' ', '-')}`}>
                                {selectedProduct.status}
                            </span>
                        </p>
                        <div className="modal-actions">
                            <button
                                onClick={() => handleToggleLike(selectedProduct.id)}
                                className="like-button"
                            >
                                {selectedProduct.likedByUser ? ' ' : ''}
                            </button>
                            <button onClick={handleBuyProduct} className="buy-button">
                                
                            </button>
                            <button onClick={handleChatStart} className="chat-button">
                                11 
                            </button>
                        </div>
                        <button className="close-button" onClick={closeModal}>
                            
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default ProductPage;
