import React, { useState, useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../App';
import '../components/styles/FreeBoardPage.css';
import apiClient from '../utils/apiClient';

function FreeBoardPage() {
    const { isLoggedIn, userInfo } = useContext(UserContext);
    const [posts, setPosts] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isEditMode, setIsEditMode] = useState(false);
    const [currentPost, setCurrentPost] = useState(null);
    const [selectedImage, setSelectedImage] = useState(null); //   
    const itemsPerPage = 5;
    const navigate = useNavigate();

    useEffect(() => {
        fetchPosts();
    }, []);

    const fetchPosts = async () => {
        try {
            const response = await apiClient.get('/api/freeboard');
            const sortedPosts = response.data.sort((a, b) => b.id - a.id);
            setPosts(sortedPosts);
        } catch (error) {
            console.error('    :', error);
        }
    };

    const handleSearchChange = (e) => {
        setSearchTerm(e.target.value);
    };

    const handlePageChange = (page) => {
        setCurrentPage(page);
    };

    const handlePostClick = async (postId) => {
        try {
            await apiClient.post(`/api/freeboard/${postId}/view`);
            navigate(`/freeboard/${postId}`);
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleLikePost = async (postId) => {
        try {
            const updatedPosts = posts.map((post) => {
                if (post.id === postId) {
                    const wasLiked = post.likedByUser;
                    return {
                        ...post,
                        likes: wasLiked ? post.likes - 1 : post.likes + 1,
                        likedByUser: !wasLiked,
                    };
                }
                return post;
            });

            setPosts(updatedPosts);

            await apiClient.post(`/api/freeboard/${postId}/like`);
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleOpenModal = (post = null) => {
        if (!isLoggedIn) {
            alert(' .');
            return;
        }

        if (post) {
            setIsEditMode(true);
            setCurrentPost(post);
        } else {
            setIsEditMode(false);
            setCurrentPost({
                title: '',
                category: '',
                content: '',
                authorUsername: userInfo?.username,
                createdAt: new Date().toISOString(),
            });
        }
        setSelectedImage(null); //  
        setIsModalOpen(true);
    };

    const handleCloseModal = () => {
        setIsModalOpen(false);
        setCurrentPost(null);
        setSelectedImage(null); //  
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setCurrentPost((prev) => ({ ...prev, [name]: value }));
    };

    const handleImageChange = (e) => {
        setSelectedImage(e.target.files[0]);
    };

    const handleSavePost = async () => {
        if (!currentPost.title.trim() || !currentPost.content.trim()) {
            alert('   .');
            return;
        }

        try {
            const formData = new FormData();
            formData.append('title', currentPost.title);
            formData.append('category', currentPost.category);
            formData.append('content', currentPost.content);
            if (selectedImage) {
                formData.append('image', selectedImage); //   
            }

            if (isEditMode) {
                await apiClient.put(`/api/freeboard/${currentPost.id}`, formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
            } else {
                await apiClient.post('/api/freeboard', formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
            }

            fetchPosts();
            handleCloseModal();
        } catch (error) {
            console.error('    :', error);
        }
    };

    const filteredPosts = posts.filter((post) =>
        post.title.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const startIndex = (currentPage - 1) * itemsPerPage;
    const currentPosts = filteredPosts.slice(startIndex, startIndex + itemsPerPage);
    const totalPages = Math.ceil(filteredPosts.length / itemsPerPage);

    return (
        <div className="free-board-page">
            <h1 className="centered"></h1>
            <div className="top-bar">
                <input
                    type="text"
                    placeholder=" ."
                    value={searchTerm}
                    onChange={handleSearchChange}
                    className="search-input"
                />
                <button className="search-button"></button>
                <button
                    onClick={() => handleOpenModal()}
                    className="create-button"
                >
                     
                </button>
            </div>
            <table className="board-table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {currentPosts.map((post, index) => (
                        <tr key={post.id}>
                            <td>{startIndex + index + 1}</td>
                            <td onClick={() => handlePostClick(post.id)}>{post.title}</td>
                            <td>{post.category || ''}</td>
                            <td>{post.authorUsername}</td>
                            <td>{new Date(post.createdAt).toLocaleString()}</td>
                            <td>{post.views}</td>
                            <td>
                                <button onClick={() => handleLikePost(post.id)}>
                                     {post.likes || 0}
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            <div className="pagination">
                {Array.from({ length: totalPages }, (_, index) => (
                    <button
                        key={index + 1}
                        onClick={() => handlePageChange(index + 1)}
                        className={currentPage === index + 1 ? 'active' : ''}
                    >
                        {index + 1}
                    </button>
                ))}
            </div>
            {isModalOpen && (
                <div className="modal">
                    <div className="modal-content">
                        <h2>{isEditMode ? ' ' : ' '}</h2>
                        <input
                            type="text"
                            name="title"
                            placeholder=""
                            value={currentPost.title}
                            onChange={handleInputChange}
                        />
                        <select
                            name="category"
                            value={currentPost.category}
                            onChange={handleInputChange}
                        >
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                        <textarea
                            name="content"
                            placeholder=" ."
                            value={currentPost.content}
                            onChange={handleInputChange}
                            rows="5"
                        ></textarea>
                        <input type="file" accept="image/*" onChange={handleImageChange} />
                        <button onClick={handleSavePost}></button>
                        <button onClick={handleCloseModal}></button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default FreeBoardPage;
