import React, { useState } from 'react';
import '../components/styles/VolunteerCreatePage.css'; //   
import axios from 'axios';

function VolunteerCreatePage() {
    const [newPost, setNewPost] = useState({
        title: '',
        content: '',
        startDate: '',
        endDate: '',
        region: '',
        contact: '',
        maxParticipants: '',
    });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setNewPost((prev) => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        console.log('New Post Data:', newPost); //   

        if (
            !newPost.title ||
            !newPost.content ||
            !newPost.startDate ||
            !newPost.endDate ||
            !newPost.region ||
            !newPost.contact ||
            !newPost.maxParticipants
        ) {
            alert('  .');
            return;
        }

        const phoneRegex = /^\d{3}-\d{3,4}-\d{4}$/;
        if (!phoneRegex.test(newPost.contact)) {
            alert('  . : 010-1234-5678');
            return;
        }

        const maxParticipants = parseInt(newPost.maxParticipants, 10);
        if (isNaN(maxParticipants) || maxParticipants <= 0) {
            alert('    .');
            return;
        }

        try {
            const response = await axios.post('/api/volunteer/create', newPost, {
                headers: {
                    'Content-Type': 'application/json',
                },
            });
            alert('  !');
            console.log('Response:', response.data);
        } catch (error) {
            console.error('Error:', error);
            alert('   .');
        }
    };

    return (
        <div className="volunteer-create-page">
            <h1 className="title">  </h1>
            <form onSubmit={handleSubmit} className="create-form">
                <div className="form-group">
                    <label htmlFor="title"></label>
                    <input
                        type="text"
                        id="title"
                        name="title"
                        placeholder=" "
                        value={newPost.title}
                        onChange={handleInputChange}
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="content"></label>
                    <textarea
                        id="content"
                        name="content"
                        placeholder=" "
                        value={newPost.content}
                        onChange={handleInputChange}
                        rows="4"
                    />
                </div>
                <div className="form-group">
                    <label>  </label>
                    <div className="date-group">
                        <input
                            type="date"
                            name="startDate"
                            value={newPost.startDate}
                            onChange={handleInputChange}
                        />
                        <span>~</span>
                        <input
                            type="date"
                            name="endDate"
                            value={newPost.endDate}
                            onChange={handleInputChange}
                        />
                    </div>
                </div>
                <div className="form-group">
                    <label htmlFor="region"></label>
                    <select id="region" name="region" value={newPost.region} onChange={handleInputChange}>
                        <option value=""> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </div>
                <div className="form-group">
                    <label htmlFor="contact"></label>
                    <input
                        type="text"
                        id="contact"
                        name="contact"
                        placeholder="  (: 010-1234-5678)"
                        value={newPost.contact}
                        onChange={handleInputChange}
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="maxParticipants"> </label>
                    <input
                        type="number"
                        id="maxParticipants"
                        name="maxParticipants"
                        placeholder="  "
                        value={newPost.maxParticipants}
                        onChange={handleInputChange}
                    />
                </div>
                <button type="submit" className="submit-button"></button>
            </form>
        </div>
    );
}

export default VolunteerCreatePage;
