import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';
import apiClient from '../utils/apiClient'; // API   

function ChatRoom() {
    const { roomKey } = useParams();
    const [messages, setMessages] = useState([]); //  
    const [message, setMessage] = useState(''); //   
    const [stompClient, setStompClient] = useState(null); // WebSocket 
    const [currentUser, setCurrentUser] = useState(null); //    

    // WebSocket     
    useEffect(() => {
        const fetchCurrentUser = async () => {
            try {
                const response = await apiClient.get('/api/users/session'); //    
                setCurrentUser(response.data);
            } catch (error) {
                console.error('     :', error);
            }
        };

        fetchCurrentUser();

        return () => {
            if (stompClient) {
                stompClient.disconnect(() => console.log('WebSocket  '));
            }
        };
    }, [stompClient]);

    // WebSocket 
    useEffect(() => {
        if (currentUser) {
            const socket = new SockJS('/ws');
            const client = Stomp.over(socket);

            client.connect(
                { username: currentUser.username },
                () => {
                    console.log('WebSocket  ');
                    client.subscribe(`/topic/chat/${roomKey}`, (payload) => {
                        try {
                            const newMessage = JSON.parse(payload.body);
                            setMessages((prevMessages) =>
                                Array.isArray(prevMessages) ? [...prevMessages, newMessage] : [newMessage]
                            );
                        } catch (error) {
                            console.error('   :', error);
                        }
                    });
                },
                (error) => {
                    console.error('WebSocket  :', error);
                }
            );

            setStompClient(client);
        }
    }, [currentUser, roomKey]);

    //   
    useEffect(() => {
        const fetchPreviousMessages = async () => {
            try {
                const response = await apiClient.get(`/api/chats/messages/${roomKey}`);
                if (Array.isArray(response.data)) {
                    setMessages(response.data);
                } else {
                    console.error('    :', response.data);
                }
            } catch (error) {
                console.error('     :', error);
            }
        };

        fetchPreviousMessages();
    }, [roomKey]);

    //   
    useEffect(() => {
        const chatMessages = document.querySelector('.chat-messages');
        if (chatMessages) {
            chatMessages.scrollTop = chatMessages.scrollHeight;
        }
    }, [messages]);

    //  
    const sendMessage = () => {
        if (!stompClient || !stompClient.connected) {
            alert('WebSocket    .');
            return;
        }
        if (message.trim() === '') {
            alert('    .');
            return;
        }

        const chatMessage = {
            sender: currentUser.username,
            content: message,
            type: 'CHAT',
        };
        stompClient.send('/app/chat.sendMessage', {}, JSON.stringify(chatMessage));
        setMessage(''); //   
    };

    if (!currentUser) {
        return <div> ...</div>;
    }

    return (
        <div>
            <h2>: {roomKey}</h2>
            <div className="chat-messages">
                {messages.map((msg, index) => (
                    <div
                        key={index}
                        className={`chat-message ${
                            msg.sender === currentUser.username ? 'my-message' : 'other-message'
                        }`}
                    >
                        <strong>{msg.sender || '  '}: </strong>
                        <span>{msg.content || ' '}</span>
                    </div>
                ))}
            </div>
            <input
                type="text"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder=" ..."
            />
            <button onClick={sendMessage}></button>
        </div>
    );
}

export default ChatRoom;
