import React, { useState, useContext, useEffect } from 'react';
import { Container, Row, Col, Form, Button, Accordion, Modal } from 'react-bootstrap';
import apiClient from './../utils/apiClient'; // Axios  fetch 
import { UserContext } from '../App';
import '../components/styles/CustomerSupport.css';

function CustomerSupport() {
    const { isLoggedIn, userInfo } = useContext(UserContext);

    //    
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        category: ' ',
        inquiry: '',
    });

    //    (InquiryResponseDto)
    const [inquiries, setInquiries] = useState([]);

    //   
    const [showModal, setShowModal] = useState(false);
    //   " "
    const [selectedInquiry, setSelectedInquiry] = useState(null);

    // ---------------------------
    // 1.     
    // ---------------------------
    useEffect(() => {
        if (isLoggedIn && userInfo) {
            setFormData({
                name: userInfo.username || '',
                email: userInfo.email || '',
                category: ' ',
                inquiry: '',
            });
        }
    }, [isLoggedIn, userInfo]);

    // ---------------------------
    // 2.    
    // ---------------------------
    useEffect(() => {
        if (isLoggedIn) {
            fetchUserInquiries();
        }
    }, [isLoggedIn]);

    const fetchUserInquiries = async () => {
        try {
            // /api/inquiries (   )
            const response = await apiClient.get('/api/inquiries');
            //  InquiryResponseDto[]  
            setInquiries(response.data);
        } catch (error) {
            console.error('   :', error.response?.data || error.message);
        }
    };

    // ---------------------------
    // 3.   
    // ---------------------------
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData((prev) => ({
            ...prev,
            [name]: value,
        }));
    };

    // ---------------------------
    // 4.   
    // ---------------------------
    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!isLoggedIn) {
            alert(' .');
            return;
        }

        if (!formData.inquiry.trim()) {
            alert('  .');
            return;
        }

        try {
            //  
            await apiClient.post('/api/inquiries', {
                category: formData.category,
                content: formData.inquiry,
            });
            alert('  .');

            //  
            setFormData((prev) => ({ ...prev, inquiry: '' }));

            //     
            fetchUserInquiries();
        } catch (error) {
            console.error('  :', error.response?.data || error.message);
            alert('  .');
        }
    };

    // ---------------------------
    // 5.   / 
    // ---------------------------
    const handleShowModal = (inquiry) => {
        setSelectedInquiry(inquiry);
        setShowModal(true);
    };

    const handleCloseModal = () => {
        setSelectedInquiry(null);
        setShowModal(false);
    };

    return (
        <Container style={{ padding: '40px 20px', marginTop: '40px' }}>

            {/*   */}
            <Row className="mb-4">
                <Col>
                    <h1 style={{ color: '#4CAF50', fontWeight: 'bold' }}></h1>
                    <p>
                            .
                         FAQ     .
                    </p>
                </Col>
            </Row>

            {/* FAQ */}
            <Row className="mb-5">
                <Col>
                    <div className="faq-section">
                        <h2 style={{ color: '#4CAF50' }}>   (FAQ)</h2>
                        <Accordion>
                            <Accordion.Item eventKey="0">
                                <Accordion.Header>Q.   ?</Accordion.Header>
                                <Accordion.Body>
                                       ''   ,
                                       .
                                </Accordion.Body>
                            </Accordion.Item>
                            <Accordion.Item eventKey="1">
                                <Accordion.Header>Q. ID/PW .    ?</Accordion.Header>
                                <Accordion.Body>
                                      'ID/PW '  ,
                                      ID       .
                                </Accordion.Body>
                            </Accordion.Item>
                            <Accordion.Item eventKey="2">
                                <Accordion.Header>Q.     ?</Accordion.Header>
                                <Accordion.Body>
                                            .
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </div>
                </Col>
            </Row>

            {/*   */}
            <Row className="mb-4">
                <Col>
                    <div className="inquiry-section">
                        <h2 style={{ color: '#4CAF50' }}></h2>
                        <Form onSubmit={handleSubmit}>
                            <Form.Group className="mb-3" controlId="formCategory">
                                <Form.Label> </Form.Label>
                                <Form.Select
                                    name="category"
                                    value={formData.category}
                                    onChange={handleInputChange}
                                >
                                    <option value=" "> </option>
                                    <option value=" "> </option>
                                    <option value=" "> </option>
                                    <option value=""></option>
                                </Form.Select>
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formName">
                                <Form.Label></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="name"
                                    value={formData.name}
                                    readOnly
                                />
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formEmail">
                                <Form.Label></Form.Label>
                                <Form.Control
                                    type="email"
                                    name="email"
                                    value={formData.email}
                                    readOnly
                                />
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formInquiry">
                                <Form.Label> </Form.Label>
                                <Form.Control
                                    as="textarea"
                                    rows={5}
                                    name="inquiry"
                                    placeholder="  "
                                    value={formData.inquiry}
                                    onChange={handleInputChange}
                                />
                            </Form.Group>

                            <Button variant="success" type="submit" className="w-100">
                                
                            </Button>
                        </Form>
                    </div>
                </Col>
            </Row>

            {/*     */}
            <Row className="mb-4">
                <Col>
                    <h2 style={{ color: '#4CAF50' }}>  </h2>
                    {inquiries.length === 0 ? (
                        <p>   .</p>
                    ) : (
                        <div>
                            {inquiries.map((inquiry) => (
                                <div
                                    key={inquiry.id}
                                    style={{
                                        border: '1px solid #ccc',
                                        borderRadius: '8px',
                                        padding: '10px',
                                        marginBottom: '10px',
                                    }}
                                >
                                    <p>
                                        <strong>:</strong> {inquiry.category}
                                    </p>
                                    <p>
                                        <strong> :</strong> {inquiry.content}
                                    </p>
                                    {/*   */}
                                    <Button
                                        variant="primary"
                                        onClick={() => handleShowModal(inquiry)}
                                    >
                                        
                                    </Button>
                                </div>
                            ))}
                        </div>
                    )}
                </Col>
            </Row>

            {/* :    +   */}
            <Modal show={showModal} onHide={handleCloseModal} centered>
                <Modal.Header closeButton>
                    <Modal.Title>  </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {selectedInquiry && (
                        <>
                            <p>
                                <strong>:</strong> {selectedInquiry.category}
                            </p>
                            <p>
                                <strong> :</strong>
                            </p>
                            <div
                                style={{
                                    backgroundColor: '#f8f9fa',
                                    padding: '10px',
                                    borderRadius: '4px',
                                    marginBottom: '10px',
                                }}
                            >
                                {selectedInquiry.content}
                            </div>

                            <hr />
                            <p>
                                <strong> :</strong>
                            </p>
                            {selectedInquiry.reply ? (
                                <div
                                    style={{
                                        backgroundColor: '#fff',
                                        border: '1px solid #dee2e6',
                                        padding: '10px',
                                        borderRadius: '4px',
                                    }}
                                >
                                    {selectedInquiry.reply}
                                </div>
                            ) : (
                                <p>   .</p>
                            )}
                        </>
                    )}
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseModal}>
                        
                    </Button>
                </Modal.Footer>
            </Modal>

        </Container>
    );
}

export default CustomerSupport;
